import React, { useState } from 'react';
import Structure from '../../Structure';
import './index.css';
import styled from 'styled-components';

const Container = styled.div`
    height: calc(100vh - 80px);
    width: 100%;

    .wrapper {
        height: 100%;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        display: flex;
        align-items: center;
    }

    @media only screen and (max-width: 767px) {
        .wrapper {
            display: block;
        }
    }
`;

const Title = styled.div`
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 10px;
`;

export default function Pricing() {
    return (
        <Structure fullpage>
            <Container>
                <div className="wrapper">
                    <div className="row">
                        <div className="col-md-6 ">
                            <div className="priceing-box">
                                <div className="price_plan">Basic</div>
                                <div style={{ flex: 1, marginTop: 15 }}>
                                    <Title>FREE</Title>
                                    <p>1,000 Searches monthly</p>
                                    <div>
                                        <div>Free for first 6 Months</div>
                                        <div>Save Previous Searches</div>
                                        <div>10 reviews per month </div>
                                    </div>
                                </div>
                                <a href="" className="price-button">
                                    JOIN
                                </a>
                            </div>
                        </div>
                        <div className="col-md-6 ">
                            <div className="priceing-box">
                                <div className="price_plan">Basic</div>
                                <div style={{ flex: 1, marginTop: 15 }}>
                                    <Title>Premium</Title>
                                    <p>unlimited Searches monthly</p>
                                    <div>
                                        <div>Free for first 6 Months</div>
                                        <div>Save Previous Searches</div>
                                        <div>unlimited reviews per month</div>
                                    </div>
                                </div>
                                <a href="" className="price-button">
                                    JOIN
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </Container>
        </Structure>
    );
}

